@media (min-width: @mobile-width) {
  .container {
    width: 280px;
    margin: 0 auto;
    padding: 0 20px;
  }
}

@media (min-width: @tablet-width) {
  .container {
    width: 708px;
    margin: 0 auto;
    padding: 0 30px;
  }
}

@media (min-width: @desktop-width) {
  .container {
    width: 1220px;
    margin: 0 auto;
  }
}
